document.addEventListener('DOMContentLoaded', function(){
    iniciarApp();
});

function iniciarApp() {
    navegacionFija();
    crearGalería();
    scrollNav();
}

// Hacemos la barra de navegacion fija al llegar a un elemento (.sobre-concierto)

function navegacionFija() {
    const barra = document.querySelector('.header');
    const sobreConcierto = document.querySelector('.sobre-conciertos');
    const body = document.querySelector('body');

    // saber que ya llegamos a ese elemento, para fijar la barra.
    window.addEventListener('scroll', function() {

        if(sobreConcierto.getBoundingClientRect().top < 0) {
            barra.classList.add('fijo');
            body.classList.add('body-scroll');
        } else {
            barra.classList.remove('fijo');
            body.classList.remove('body-scroll');
        }
    });
}

//Hace un scroll suave hacia cada seccion donde haya un id

function scrollNav() { 
    const enlaces = document.querySelectorAll('.navegacion-principal a');

    enlaces.forEach(enlace => {
        enlace.addEventListener('click', function(e) {
            e.preventDefault();

            const seccionScroll = e.target.attributes.href.value;
            const seccion = document.querySelector(seccionScroll);
            seccion.scrollIntoView({ behavior: "smooth"});
        });
    });
}

function crearGalería() {
    const galeria = document.querySelector('.galeria-imagenes');

    for(let i = 1; i<=12; i++) {
        const imagen = document.createElement('picture');
        imagen.innerHTML = `
            <source srcset="build/img/thumb/${i}.avif" type="image/avif">
            <source srcset="build/img/thumb/${i}.webp" type="image/webp">
            <img loading="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" alt="Imagen galeria">
        `;

        imagen.onclick = function(){
            mostrarImagen(i);
        }
         
        galeria.appendChild(imagen);
    }
}

function mostrarImagen(id) {
    const imagen = document.createElement('picture');
        imagen.innerHTML = `
            <source srcset="build/img/grande/${id}.avif" type="image/avif">
            <source srcset="build/img/grande/${id}.webp" type="image/webp">
            <img loading="lazy" width="200" height="300" src="build/img/grande/${id}.jpg" alt="Imagen galeria">
        `;

        // Crea el overlay con la imagen
        const overlay = document.createElement('DIV'); // Creamos un div
        overlay.appendChild(imagen); // Creamos un hijo 'picture' 
        overlay.classList.add('overlay'); // Agregamos la clase overlay
        overlay.onclick = function() {
            const body = document.querySelector('body');
            body.classList.remove('fijar-body');
            overlay.remove();
        }

        // Boton para cerrar el modal
        const cerrarModal = document.createElement('P');
        cerrarModal.textContent = 'X';
        cerrarModal.classList.add('btn-cerrar');

        cerrarModal.onclick = function() {
            const body = document.querySelector('body');
            body.classList.remove('fijar-body');
            overlay.remove();
        }

        

        

        overlay.appendChild(cerrarModal);

        // Añadirlo al HTML
        const body = document.querySelector('body'); // creamos un selector de 'body, donde vamos a poner la imagen'
        body.appendChild(overlay); // Creamos un hijo al 'body' con las características de 'overlay'
        body.classList.add('fijar-body');
}